.LandingCarouselContainer {
    background-color: #212121;
    color: white;
}

.LandingCarouselContainer > .slick-slider > .slick-dots li button::before {
    color: white !important;
}

ul.star-list {
    list-style: none;
    margin-left: 0;
    padding-left: 0;
}

ul.star-list > li {
    padding-left: 1em;
    text-indent: -1em;
}

ul.star-list > li:before {
    content: "\2605";
    padding-right: 5px;
}
